
@import "src/common/styles/mixins/mixins";
@import "src/common/styles/variables/variables";

.highlight {
  color: #FF5253;
}

.bookList__item {
  display: flex;
  flex-direction: row;
  width: 100%;
  min-height: 218px;
  padding: 24px 24px 24px 16px;
  gap: 16px;
  box-shadow: 0px 2px 4px rgba(191, 196, 201, 0.2), 0px 3px 4px rgba(191, 196, 201, 0.18), 0px 1px 5px rgba(191, 196, 201, 0.24);
  border-radius: 12px;

  @media (max-width: 768px) {
    max-width: 640px;
    padding: 16px 24px 16px 16px;
  }

  @media (max-width: 320px) {
    max-width: 288px;
    min-height: 132px;
    padding: 16px 16px 16px 8px;
    gap: 8px;
  }

  .bookList__item_coverWrapper {
    display: flex;
    align-items: center;
    justify-content: center;

    @media (max-width: 768px) {
      align-items: flex-start;
    }

    .bookList__item_cover {
      border-radius: 3px;
      border: 1px solid $unActive-color;
      width: 120px;
      height: 170px;
      background: #F9F9FA;
      box-sizing: border-box;
      object-fit: cover;

      @media (max-width: 320px) {
        width: 70px;
        height: 100px;
      }
    }
  }

  .bookList__item_infoWrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 24px;

    @media (max-width: 768px) {
      max-width: 464px;
      gap: 16px;
    }

    @media (max-width: 320px) {
      max-width: 186px;
      gap: 3px;
    }

    .bookList__item_info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      min-height: 78px;
      gap: 8px;

      @media (max-width: 320px) {
        gap: 3px;
        min-height: 35px;
        justify-content: flex-start;
      }

      .bookList__item_info_title {
        min-height: 30px;
        letter-spacing: 0;
        @include text(24px, 30px, 700, $main-text-color);

        @media (max-width: 320px) {
          @include text(14px, 18px, 600, $main-text-color);
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          line-height: 1.3em;
          min-height: 18px;
          max-height: 54px;
        }
      }

      .bookList__item_info_author {
        width: 174px;
        @include text(16px, 24px, 400, $unActive-color);

        @media (max-width: 320px) {
          @include text(12px, 18px, 400, $main-text-color);
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          line-height: 1.3em;
          min-height: 18px;
          max-height: 54px;
        }
      }
    }

    .bookList__item_buttonBlock {
      display: flex;
      justify-content: space-between;
      align-items: center;

      @media (max-width: 320px) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        gap: 16px;
      }

      .bookList__item_button {
        width: 174px;
        height: 40px;
        align-items: flex-end;

        @media (max-width: 320px) {
          width: 186px;
        }
      }
    }

  }
}